.panel {
  width: 100%;
  height: var(--navbar-height);
  background-color: var(--bg-color-inverted);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  &__login{
    text-decoration: none;
    font-size: var(--font-size-l);
  }
  &__avatar{
    width: 40px;
    height: 40px;
  }
}